import React, { Component } from 'react';
import Container from './Container';

class VideoBackground extends Component {
    constructor(props) {
        super(props);

        this.state = { playing: true };
    }

    togglePlayingState = () => {
        this.setState({ playing: !this.state.playing });
        this.refs.vidRef.pause();
    }

    pause = () => {
        this.togglePlayingState();
        this.refs.vidRef.pause();
    }

    play = () => {
        this.togglePlayingState();
        this.refs.vidRef.play();
    }

    render() {
        return (
            <div>
                <div className="video-container">
                    <video ref="vidRef" autoPlay muted loop preload="auto" id="myVideo" >
                        <source src="/video/pico_bg.mp4" type="video/mp4" />
                        <p>Your browser doesn't support HTML5 video. Here is
                        a <a href="/video/pico_bg.mp4">link to the video</a> instead.</p>
                    </video>

                    <div className="video-overlay">
                        {(this.state.playing === true) &&
                            <span className="icon" onClick={this.pause}>
                                <i className="fas fa-pause"></i>
                            </span>
                        }
                        {(this.state.playing !== true) &&
                            <span className="icon" onClick={this.play}>
                                <i className="fas fa-play"></i>
                            </span>
                        }
                    </div>
                </div>

                <section className="section" key="0">
                    <div className="columns is-centered is-mobile">
                        <Container>
                            <h1 className="welcome-title has-text-black">newest devices</h1>
                            <p className="is-size-3 has-workflow-spacing">
                                We are excited to offer our skin resurfacing, hair removal and <b>tattoo removal</b> services in the Northland with the <b><span className="has-text-link">newest devices</span></b> that are not available in the Kansas City region.
                            </p>

                            <div className="columns is-centered is-mobile">
                                <div className="column is-12-desktop is-12-touch">
                                    Tattoos are a personal expression that can represent a significant person, symbol, or event that we want to remember. While most are happy with their tattoos long term, research shows that <b>57% of people eventually regret their decision to get a tattoo. That’s where we come in!</b>
                                    <br />
                                    <br />
                                    <b>Why Should I Get My Tattoo Removed?</b>
                                    Is the name of someone from a past relationship staining your skin? Is your new job or career against having visible tattoos? Are you preparing for a special event you want clear skin for? These are just a few of the most common reasons people are choosing to have their tattoos removed using laser tattoo removal. Tattoos are meant to be a form of self-expression, and while initially you may have been excited about your tattoo, people and feelings change, and your tattoo should have the same ability. If this sounds like you, we have a solution to your “permanent ink!"
                                </div>
                            </div>
                        </Container>
                    </div>
                </section>
            </div>
        );
    }
}

export default VideoBackground;
