import React from 'react';

import CallToAction from './CallToAction';
import Container from './Container';
import TimelyBookButton from './TimelyBookButton';

const TattooInfo = () => [
    <section className="section" key="0">
        <div className="columns is-centered is-mobile">
            <Container>
                <h1 id="how_it_works" className="welcome-title has-text-black">how it works</h1>
                <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">welcome to the future of getting rid of the past...</h5>
                <p className="has-workflow-spacing">
                    Pulses of high-intensity laser light remove your tattoo by breaking up the tattoo ink into extremely small
                    particles. Your immune system’s white blood cells fight off the ink particles, and are naturally carried out
                    of the body, resulting in a tattoo that is lighter in color or completely removed. Recent advancements in
                    technology introduced new laser devices that can safely remove tattoos efficiently regardless of skin
                    color or skin type.
                </p>

                <div className="columns is-vcentered has-workflow-spacing is-double">
                    <div className="column is-4 has-text-centered">
                        <img src="/images/piqo.jpg" alt="PiQo" />
                    </div>
                    <div className="column">
                        <div>
                            <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">meet the technology</h5>
                            <p>
                                At Undo, Skincare Center, we use the most advanced technology in the Midwest to
                                make the tattoo process easy and painless! The Lumenis PiQo4 laser is the new gold
                                standard for producing excellent laser tattoo removal outcomes. PiQo4 treats a
                                wide range of pigment colors and its versatile energy matrix ensures effective
                                shattering of pigment deposits. It also has the largest spot size on the market, which
                                allows you to deliver faster treatments. The PiQo4 laser uses four true laser
                                wavelengths, Q switching technology – PICO + NANO wavelengths in order to treat
                                the broadest range of pigment/tattoo color, and the highest consistent laser beam
                                power to effectively break up a wide range of colors, even difficult dark blues and
                                greens. Utilizing the largest square beam, up to 15mm, the PiQo4 laser is able to
                                distribute energy evenly throughout your tissue allowing for a more efficient
                                breakup of ink particles while avoiding leaving hot spots.
                            </p>
                        </div>
                    </div>
                </div>

                <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">Pico+Nano for doubly effective treatment</h5>
                <p className="has-workflow-spacing is-double">
                    Clinical studies have shown that the optimal method for shattering pigment particles is to treat them with
                    both Nano and Picosecond laser pulses.
                </p>
                <div className="has-text-centered has-workflow-spacing">
                    <img src="/images/Rocks-To-Pebbles-To-Sand.png" alt="Rocks To Pebbles To Sand" />
                </div>
                <p className="has-workflow-spacing is-double">
                    First, the Nanosecond pulses are used to deliver short bursts of energy that break down the larger and
                    deeper pigment or ink particles. Then, the area is rescanned with Picosecond pulses that dismantle the
                    smaller and shallower particles. This combination yields far better results than using either one of the
                    technologies on its own.
                </p>


                <div className="columns is-vcentered">
                    <div className="column">
                        <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">4 Wavelengths – For effective shattering of the most popular pigment/ink colors</h5>
                        <p className="has-workflow-spacing">
                            Since each pigment color absorbs a specific wavelength, PiQo4 offers 4 wavelengths in order to treat the broadest range of pigment/tattoo colors, on the widest range of skin types. Its four wavelengths – 1064nm, 532nm, 650nm and 585nm – target 9 of the most frequently used tattoo colors ranging from light orange to dark black. With PiQo4  you can erase the rainbow.
                        </p>
                    </div>
                    <div className="column is-4 has-text-centered">
                        <img src="/images/PiQo4-Wheel.png" alt="PiQo4 wheel" />
                    </div>
                </div>
            </Container>
        </div>
    </section>,

    <CallToAction key="1" text="At <b>Undo, Skincare Center</b>, we use only the best and most advanced technology to make the tattoo removal process easy and painless!" />,

    <section className="section" key="2">
        <div className="columns is-centered is-mobile">
            <Container>
                <h1 id="pricing" className="welcome-title has-text-black">pricing</h1>
                <p className="has-workflow-spacing is-size-3">
                    So you're ready to <b>remove your tattoo</b>, but you’re worried about the cost of treatments?
                    <b><span className="has-text-link">&nbsp;Don’t turn down tattoo removal treatments</span></b> because of the price! Undo Laser Skincare
                    Clinic provides treatments designed to fit anyone's budget!
                </p>

                <div className="has-workflow-spacing is-double">
                    <TimelyBookButton />
                </div>

                <p className="has-workflow-spacing">
                    Tattoo removal is actually more affordable than you think! There are several
                    factors that determine how much it will cost to remove your tattoo. Dermatologists
                    and skin experts categorized these factors and identified three major elements
                    that will determine how much it will cost.
                </p>
                <p className="has-workflow-spacing">
                    <b>Size:</b><br />
                    Size is the number one factor in determining how much it will cost to remove
                    your tattoo. Undo, Laser Skincare Center measures tattoos in compressed ink square
                    inches, and we charge per square inch to remove a tattoo.
                </p>
                <p className="has-workflow-spacing">
                    <b>Location:</b><br />
                    Where your tattoo is matters when determining the cost of removal. If your tattoo
                    is in a sensitive location, session times will be reduced to prevent an adverse
                    reaction or damage to the outer layer of your skin. This will increase the number
                    of sessions needed to completely remove the tattoo.
                </p>
                <p className="has-workflow-spacing">
                    <b>Quality:</b><br />
                    Quality of the tattoo pertains to the color, design, density or thickness, and
                    the type of ink used in your tattoo. Laser tattoo removal is more successful on
                    darker shades such as black, dark blue, and red but lighter shades such as orange,
                    yellow, and green take longer to remove which increases the number of sessions
                    needed. In addition, more detailed designs take longer. Density refers to the type
                    of pigment used in a tattoo and how thick the ink is. Thicker pigments require
                    more work from the laser to break down and the body's immune system has to work
                    harder to remove the broken particles.
                </p>
                <div className="columns is-centered has-workflow-spacing">
                    <div className="column is-10 has-text-centered">
                        <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">
                            Pricing starting at $129 per square inch of compressed ink.
                        </h5>
                    </div>
                </div>




            </Container>
        </div>
    </section>
];

export default TattooInfo;