import React from 'react';
import TimelyBookButton from './TimelyBookButton';
import Container from './Container';

const ContactUs = () => (
    <section id="contact" className="section has-background-info">
        <div className="columns is-centered is-mobile">
            <Container>
                <h1 className="welcome-title has-text-white">contact us</h1>
                <p className="has-text-white is-size-3 has-workflow-spacing is-double">Take the next step and begin removing your unwanted ink! Call us <b>today</b> and schedule your FREE consulation!</p>

                <TimelyBookButton container="has-text-centered has-workflow-spacing is-double" className="button is-black is-inverted timely" />

                <p className="has-text-centered is-size-4 has-text-white">
                    Questions about tattoo removal?
                </p>
                <p className="has-text-centered is-size-4 has-workflow-spacing is-double has-text-white">
                    We're here for you!
                </p>

                <div className="columns is-vcentered">
                    <div className="column is-6 has-text-centered has-text-white">
                        <p className="slight-spacing">Undo, Skincare Center</p>
                        <p className="slight-spacing">6442 NW Crooked Rd.</p>
                        <p className="slight-spacing">Parkville, MO 64152</p>
                    </div>
                    <div className="column is-6 has-text-centered has-text-white">
                        <a className="icon is-large facebook has-text-white has-workflow-spacing" href="https://www.facebook.com/undotoday/" target="_blank" rel="noopener noreferrer">
                            <i className="fab fa-facebook fa-3x" />
                        </a>

                        <div className="icons-with-text">
                            <span className="icon">
                                <i className="fas fa-phone" />
                            </span>
                            <a href="tel:8169748636" className="has-text-white">(816) 974-8636</a>
                        </div>
                        <div className="icons-with-text">
                            <span className="icon">
                                <i className="fas fa-envelope"></i>
                            </span>
                            <a href="mailto:info@undo.today" className="has-text-white">info@undo.today</a>
                        </div>
                        <div className="icons-with-text">
                            <span className="icon">
                                <i className="fas fa-globe-americas"></i>
                            </span>
                            <a href="/" className="has-text-white">https://undo.today</a>
                        </div>
                    </div>
                </div>
            </Container>
        </div>
    </section>
);

export default ContactUs;