import React, { Component } from 'react';

class RadialChart extends Component {
    state = {}

    componentDidMount() {
        // For initial animation
        setTimeout(() => {
            this.setState({ setStrokeLength: true });
        });
    }

    render() {
        const { setStrokeLength } = this.state;
        const {
            radius,
            progress,
            strokeWidth,
            dimension,
            color
        } = this.props;

        const circleRadius = Math.min(radius, 85);
        const circumference = 2 * 3.14 * circleRadius;
        const strokeLength = setStrokeLength ? circumference / 100 * progress : 0;
        return (
            <div className={'radial-chart'}>
                <svg viewBox="0 0 180 180" width={dimension} height={dimension}>
                    <circle
                        className="radial-chart-total"
                        stroke={color}
                        strokeWidth={strokeWidth}
                        fill="none"
                        cx="90"
                        cy="90"
                        r={circleRadius}
                    />
                    <circle
                        className="radial-chart-progress"
                        stroke={color}
                        strokeWidth={strokeWidth}
                        strokeDasharray={`${strokeLength},${circumference}`}
                        strokeLinecap="round"
                        fill="none"
                        cx="90"
                        cy="90"
                        r={circleRadius}
                    />
                    <text className="radial-text" x="50%" y="50%" textAnchor="middle" stroke="#dbdbdb" strokeWidth="1px" dy=".3em">{progress}%</text>
                </svg>
            </div>
        );
    }
}

RadialChart.defaultProps = {
    radius: 60,
    progress: 100,
    strokeWidth: 15,
    dimension: 180,
    color: '#dbdbdb'
};

export default RadialChart;